* {
	box-sizing: border-box;
	outline: none; }
body {
	@include reg; }
img {
	max-width: 100%; }
.container {
	width: 1170px;
	margin: auto; }
#back-top {
    position: fixed;
    bottom: 0px;
    padding: 0px;
    left: 56.5%;
    margin-left: 500px;
    z-index: 99991; }

#back-top a {
    width: 77px;
    height: 30px;
    background: rgba(24, 217, 207, 1);
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    -webkit-transition: all linear 0.2s;
    -moz-transition: all linear 0.2s;
    -ms-transition: all linear 0.2s;
    -o-transition: all linear 0.2s;
    transition: all linear 0.2s;
    text-align: center; }

#back-top a:hover {
    height: 70px;
    display: block;
    background: #663399;
    -webkit-transition: all linear 0.2s;
    -moz-transition: all linear 0.2s;
    -ms-transition: all linear 0.2s;
    -o-transition: all linear 0.2s;
    transition: all linear 0.2s;
    color: #fff; }
