.navbar {
	padding: 8px 0;
	position: relative;
	&-block {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__menu {
		display: flex;
		align-items: center;
		text-align: right;
		margin-right: 69px; }
	&__logo {
 }		// margin-right: 130px
	&__phone {
		margin-right: 32px;
		margin-left: 5px;
		text-decoration: none;
		color: $black;
		font-family: 'ProximaNovaCond-Regular'; }
	&__button {
		padding: 10px 15px;
		outline: none;
		border: none;
		font-family: 'ProximaNovaCond-Regular'; } }
.logo-text {
	color: #010101;
	font-size: 15px;
	text-transform: uppercase;
	@include bold;
	display: block; }
.contacts-wrap {
	background-color: $bg; }
.menu {
	&__items {
		display: flex;
		justify-content: space-between;
		margin-left: 70px; }
	&__link {
		text-decoration: none;
		color: $text-color;
		@include reg;
		&:hover {
			color: $main-color;
			border-bottom: 3px solid $main-color; } }
	&__item {
		list-style: none;
		margin-right: 60px;
		&:after {
			content: '|';
			margin-left: 30px;
			position: absolute;
			top: 40%; }
		&:last-child {
			&:after {
				display: none; } } }
