.answers {
	width: 100%;
	height: 100%;
	background: linear-gradient(to right, $bg 60%, $white 40%);
	margin-bottom: 150px;
	padding: 95px 60px;
	display: flex;
	position: relative;
	&-block {
		display: flex;
		flex-wrap: wrap;
		width: 60%;
		&__list {
			float: right;
			margin-right: -80px;
			margin-top: -423px; } }
	&__title {
		margin-left: -45px;
		background-color: $main-color;
		text-align: center;
		text-transform: uppercase;
		font-size: 28px;
		line-height: 28px;
		padding: 19px 65px;
		@include bold;
		display: block;
		max-width: 317px;
		margin-top: -129px;
		margin-bottom: 70px; }
	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		background-color: $white;
		padding: 17px 25px;
		width: 200px;
		height: 170px;
		margin-bottom: 40px;
		margin-right: 33px;
		text-align: center; }
	&__text {
		display: flex;
		justify-content: center;
		margin-top: 20px;
		line-height: 28px;
		font-size: 18px;
		text-transform: uppercase;
		width: 160px;
		// &__list
		// 	display: block
 }		// 	width: 40%
	&__wrapper {
		margin-bottom: 70px;
		width: 404px; }
	&__number {
		font-size: 35px;
		color: $main-color;
		@include bold;
		&:after {
			content: '';
			background-image: url('../img/line.png');
			width: 32px;
			height: 2px;
			display: inline-block;
			vertical-align: middle;
			margin-left: 5px; } } }
.list__item {
	list-style: none;
	font-size: 20px;
	margin-left: 15px; }
.clearfix {
	clear: both; }


