.offer {
	margin-top: 100px;
	// margin-bottom: 80px
	background-color: $bg;
	width: 100%;
	// opacity: 0.55
	position: relative;
	&-block {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding-top: 105px;
		padding-bottom: 120px;
		margin-bottom: 150px; }
	&__item {
		margin-top: 21px;
		position: relative;
		z-index: 99;
		color: $white;
		&:hover {
			color: $black;
			@include bold; } }
	&__image {
		filter: brightness(30%);
		&:hover {
			filter: brightness(80%);
			transition: 0.3s; } }
	&__wrapper {
		position: absolute;
		top: 18%;
		left: 18%;
		width: 257px;
		font-size: 20px;
		line-height: 26px; }
	&__title {
		text-align: center; }
	&__button {
		background-color: $main-color;
		padding: 11px 20px;
		font-size: 15px;
		text-transform: uppercase;
		@include bold;
		position: absolute;
		top: 57%;
		left: 33%;
		color: $black;
		&:hover {
			background-color: $black;
			color: $white;
			cursor: pointer; } } }
.section__title {
	// position: absolute
	// top: -4%
	// left: 4%
	margin-left: -45px;
	background-color: $main-color;
	text-align: center;
	text-transform: uppercase;
	font-size: 28px;
	line-height: 28px;
	padding: 19px 65px;
	@include bold;
